import Header from "@/app/components/Header";
import Footer from "@/app/components/Footer";
import Breadcrumb from "@/app/components/Breadcrumb";
import PropertyCard from "@/app/components/PropertyCard";
import Link from "next/link";
import { notFound } from "next/navigation";
import connectDB from "@/lib/mongodb";
import Property from "@/models/Property";
import FooterModel from "@/models/Footer";

export const dynamic = "force-dynamic";

export const metadata = {
  title: "Our Properties - Ajit Properties",
};

const PROPERTIES_PER_PAGE = 12;
const ALLOWED_CATEGORIES = [
  "apartment",
  "house",
  "villa",
  "condo",
  "land",
  "commercial",
];

/* ----------------------------- PROPERTIES ----------------------------- */
async function getPropertiesWithPagination(page = 1, category, filters = {}) {
  await connectDB();

  const safePage = page > 0 ? page : 1;
  const skip = (safePage - 1) * PROPERTIES_PER_PAGE;

  const filter = {
    available: true,
    type: category,
    ...filters,
  };

  const [properties, totalCount] = await Promise.all([
    Property.find(filter)
      .sort({ createdAt: -1 })
      .skip(skip)
      .limit(PROPERTIES_PER_PAGE)
      .lean(),
    Property.countDocuments(filter),
  ]);

  return {
    properties: properties.map((p) => ({
      ...p,
      _id: p._id.toString(),
    })),
    totalPages: Math.max(1, Math.ceil(totalCount / PROPERTIES_PER_PAGE)),
    currentPage: safePage,
  };
}

/* ------------------------------- FOOTER ------------------------------- */
async function getFooterData() {
  await connectDB();
  return await FooterModel.findOne().lean();
}

/* ============================== PAGE ============================== */
export default async function PropertiesPage({ params, searchParams }) {
  const { id } = await params;
  const categoryParam = id?.toLowerCase();

  if (!categoryParam || !ALLOWED_CATEGORIES.includes(categoryParam)) {
    notFound();
  }

  const { page, bedrooms, bathrooms, area, minPrice, maxPrice, location } =
    searchParams;

  const filters = {};
  if (bedrooms) filters.bedrooms = { $gte: parseInt(bedrooms) };
  if (bathrooms) filters.bathrooms = { $gte: parseInt(bathrooms) };
  if (area) filters.area = { $gte: parseInt(area) };
  if (minPrice || maxPrice) {
    filters.price = {};
    if (minPrice) filters.price.$gte = parseInt(minPrice);
    if (maxPrice) filters.price.$lte = parseInt(maxPrice);
  }
  if (location) filters.location = { $regex: location, $options: "i" };

  const { properties, totalPages, currentPage } =
    await getPropertiesWithPagination(page, categoryParam, filters);

  const footerData = await getFooterData();

  const categoryTitle =
    categoryParam.charAt(0).toUpperCase() + categoryParam.slice(1);

  const currentFilters = {
    bedrooms,
    bathrooms,
    area,
    minPrice,
    maxPrice,
    location,
  };

  function buildQuery(params) {
    const q = new URLSearchParams();
    Object.entries(params).forEach(([k, v]) => v && q.set(k, v));
    return q.toString();
  }

  return (
    <>
      <Header />

      <Breadcrumb
        title={`${categoryTitle} Properties`}
        items={[
          { label: "Home", link: "/" },
          { label: "Properties", link: "/properties" },
          { label: categoryTitle },
        ]}
      />

      {/* ================= MAIN SECTION ================= */}
      <section className="section-padding">
        <div className="container">
          <div className="row">
            {/* ================= FILTER SIDEBAR ================= */}
            <aside className="col-lg-3 col-md-4 mb-4">
              <div
                style={{
                  background: "#fff",
                  padding: "24px",
                  borderRadius: "12px",
                  boxShadow: "0 6px 18px rgba(0,0,0,0.06)",
                  position: "sticky",
                  top: "90px",
                  maxHeight: "calc(100vh - 120px)",
                  overflowY: "auto",
                }}
              >
                <h5 className="fw-bold mb-4">Filter Properties</h5>

                <form method="GET" action={`/properties/${categoryParam}`}>
                  {[
                    ["Location", "location", location],
                    ["Min Bedrooms", "bedrooms", bedrooms],
                    ["Min Bathrooms", "bathrooms", bathrooms],
                    ["Min Area (sq ft)", "area", area],
                  ].map(([label, name, value]) => (
                    <div className="mb-3" key={name}>
                      <label className="form-label fw-semibold">{label}</label>
                      <input
                        type={name === "location" ? "text" : "number"}
                        name={name}
                        defaultValue={value || ""}
                        className="form-control"
                        style={{ borderRadius: "8px", fontSize: "14px" }}
                      />
                    </div>
                  ))}

                  <div className="mb-3">
                    <label className="form-label fw-semibold">
                      Price Range
                    </label>
                    <input
                      type="number"
                      name="minPrice"
                      placeholder="Min"
                      defaultValue={minPrice || ""}
                      className="form-control mb-2"
                      style={{ borderRadius: "8px" }}
                    />
                    <input
                      type="number"
                      name="maxPrice"
                      placeholder="Max"
                      defaultValue={maxPrice || ""}
                      className="form-control"
                      style={{ borderRadius: "8px" }}
                    />
                  </div>

                  <button
                    className="btn btn-primary w-100 mb-2"
                    style={{
                      borderRadius: "30px",
                      fontWeight: 600,
                      padding: "10px",
                    }}
                  >
                    Apply Filters
                  </button>

                  <a
                    href={`/properties/${categoryParam}`}
                    className="btn btn-outline-secondary w-100"
                    style={{
                      borderRadius: "30px",
                      fontWeight: 600,
                      padding: "10px",
                    }}
                  >
                    Clear Filters
                  </a>
                </form>
              </div>
            </aside>

            {/* ================= PROPERTY GRID ================= */}
            <div className="col-lg-9 col-md-8" style={{ minHeight: "600px" }}>
              {properties.length > 0 ? (
                <>
                  <div className="row g-4">
                    {properties.map((property) => (
                      <PropertyCard key={property._id} property={property} />
                    ))}
                  </div>

                  {totalPages > 1 && (
                    <div className="page-nav-wrap text-center pt-5">
                      <ul>
                        {currentPage > 1 && (
                          <li>
                            <Link
                              className="page-numbers icon"
                              href={`/properties/${categoryParam}?${buildQuery({
                                ...currentFilters,
                                page: currentPage - 1,
                              })}`}
                            >
                              <i className="fa-solid fa-arrow-left-long"></i>
                            </Link>
                          </li>
                        )}

                        {Array.from(
                          { length: Math.min(totalPages, 10) },
                          (_, i) => i + 1,
                        ).map((pageNum) => (
                          <li key={pageNum}>
                            <Link
                              className={`page-numbers ${
                                pageNum === currentPage ? "current" : ""
                              }`}
                              href={`/properties/${categoryParam}?${buildQuery({
                                ...currentFilters,
                                page: pageNum,
                              })}`}
                            >
                              {pageNum.toString().padStart(2, "0")}
                            </Link>
                          </li>
                        ))}

                        {currentPage < totalPages && (
                          <li>
                            <Link
                              className="page-numbers icon"
                              href={`/properties/${categoryParam}?${buildQuery({
                                ...currentFilters,
                                page: currentPage + 1,
                              })}`}
                            >
                              <i className="fa-solid fa-arrow-right-long"></i>
                            </Link>
                          </li>
                        )}
                      </ul>
                    </div>
                  )}
                </>
              ) : (
                <div className="text-center py-5">
                  <h3>No properties found</h3>
                  <p>Try adjusting filters</p>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>

      <Footer data={footerData} />
    </>
  );
}
